/**
 * socketIO 
 */
import io from 'socket.io-client'
import { getItem } from '../utils'
import * as Event from './event'
import { socketUrl } from '../config/config'

export const MessServerConnect = () => {
    
    const socket = io.connect(socketUrl, {
        query: `token=${getItem('token')}`,
        reconnection: true
    }); 
    
    socket.on('connect', () => Event.connect(socket)); // 
    
    socket.on('init', message => Event.init(message)); // 
    
    socket.on('join', message => Event.join(message)); //  join 
    
    socket.on('message', message => Event.message(message)); //  message 

    socket.on('withdraw', message => Event.withdraw(message)); // 

    socket.on('addfriend', message => Event.addFriend(message)); // 

    socket.on('handlefriend', message => Event.handleFriend(message)); // 
    
    socket.on('authError', () => Event.authError()); // 
    
    socket.on('ping', message => null); // 

    socket.on('connect_error', message => Event.connectError(message)); // 
    
    socket.on('disconnect', err => Event.disconnect(err)); // 
}
